
const ser_timed = function (e, cn = false, short = false) {
    if (e == null || e == undefined) {
        return '--'
    } else {
        const year = e.substring(0, 4)
        const month = e.substring(5, 7)
        const day = e.substring(8, 10)
        const hour = e.substring(11, 13)
        const minute = e.substring(14, 16)

        if (cn) {
            if (short) {
                return year + '年' + month + '月' + day + '日 '
            }
            return year + '年' + month + '月' + day + '日 ' + hour + '時' + minute + '分'
        }
        
        if (short) {
            return year + '-' + month + '-' + day
        }
        return year + '-' + month + '-' + day + ' ' + hour + ':' + minute
    }
}

export default {

  ser_timed,
  is_now_month(e) {
    const d = new Date()
    const yyyy = d.getFullYear()
    const mm = d.getMonth() + 1

    const res = ser_timed(e, false, true).split('-')
    
    if (
        (yyyy == res[0]) && 
        (mm == res[1])
    ) {
      return true
    }
    return false
  },
  getNowDate(f) {
    const d = new Date()
    let res = d.getFullYear()
    if (f == 'mm') {
        res = d.getMonth() + 1
    } else if (f == 'dd') {
        res = d.getDate()
    }
    return res
  },
  getYearList(start_year = 2019) {
    const res = []
    const d = new Date()
    const yyyy = d.getFullYear()

    for (let i= start_year; i<= (yyyy + 1); i++  ) {
        res.push(i)
    }

    return res
  },
  isLeapYear(y) {
    if ((y % 4 == 0) && (y % 100 != 0) || (y % 400 == 0)) {
        return true
    } 
    return false
  }
}


